<template>
  <transition name="fade">
    <div class="toast" v-if="show">
      <div class="pay">
        <p class="pay-title">购买服务</p>
        <table class="pay-table" cellspacing="0" cellpadding="0" width="100%">
          <tr>
            <td class="td-tile">已选服务：</td>
            <td class="td-cont">
              <p>保洁服务</p>
              <p>代驾服务</p>
            </td>
          </tr>
          <tr>
            <td class="td-tile">服务价格：</td>
            <td class="td-cont">
              <span class="price">￥888.00</span>
              <div class="nums">
                <div class="count">-</div>
                <input type="number">
                <div class="count">+</div>
              </div>
              <span class="total-price">￥888.00</span>
            </td>
          </tr>
          <tr>
            <td class="td-tile">服务备注：</td>
            <td class="td-cont">
              <textarea name="desc"></textarea>
            </td>
          </tr>
        </table>
        <div class="btn" @click="$router.push('/order');show=false">提交</div>
        <img class="close" @click="show = false" src="/static/pay/shop-close.png" alt="">
      </div>
    </div>
  </transition>
</template>

<script>
    export default {
        name: "toast",
        data(){
          return{
            show:false
          }
        },
        created(){
          this.bus.$on('open', ()=> {
            this.show = true
          })
        }
    }
</script>

<style scoped lang="stylus">
.toast
  position fixed
  width 100%
  height 100%
  overflow hidden
  background-color rgba(0,0,0,0.5)
  z-index 666
  top 0
  left 0
  .pay
    position relative
    width 850px
    height 550px
    margin 191px auto
    background-color white
    z-index 667
    padding 30px 40px
    &-title
      font-size 24px
      font-weight bold
      color #333
      margin-bottom 30px
    &-table
      width 100%
      tr
        display block
        margin-bottom 20px
        .td-tile
          font-size 16px
          color #333
          vertical-align top
        .td-cont
          width 670px
          .price,.nums,.count,.number,.total-price
            display inline-block
            font-size 16px
          .price
            margin-right 100px
          .nums
            position relative
            height 30px
            line-height 21px
          .count
            font-size 24px
            width 20px
            height 30px
            margin 0 -5px
            text-align center
            background-color #ccc
          .total-price
            margin-left  200px
          input
            width 50px
            height 30px
            text-align center
            border 1px solid #ccc
            outline none
          input::-webkit-outer-spin-button,input::-webkit-inner-spin-button
            -webkit-appearance: none
          input[type="number"]
            -moz-appearance: textfield
          textarea
            width 670px
            height 180px
            resize none
            background-color #f2f2f2
            padding 10px
  .btn
    width 150px
    height 40px
    margin 50px auto 30px
    font-size 16px
    color #038aeb
    border 1px solid #038aeb
    text-align center
    line-height 40px
    vertical-align middle
    border-radius 6px
    cursor pointer
    &:hover
      background-color #038aeb
      color white
  img.close
    position absolute
    top 20px
    right 20px
    cursor pointer
</style>



