<template>
    <div  class="order">
      <div class="container">
        <a class="back" @click="$router.go(-1)">返回前一页</a>
        <h1>我的收银台</h1>
        <div class="order-detail">
          <div class="icon">
            <img src="" alt="">
          </div>
          <div class="bill">
            <p>交易名称：服务名称服务名称</p>
            <p>订单号：12365412365442325</p>
          </div>
          <div class="total">
            付款金额：<span style="color: #fe7405;font-size: 24px"> ￥888.00</span>
          </div>
        </div>
        <div class="pay-type">
          <div class="pay" :class="{checkborder:checked===1}">
            <i class="select-type" @click="checked = 1" :class="{checked:checked===1}"></i><span>第三方支付：</span>
            <input id="ali" type="radio" name="payment">
            <label for="ali" class="ali"></label>
            <input id="tencent" type="radio" name="payment">
            <label for="tencent" class="tencent"></label>
          </div>
          <div class="pay" :class="{checkborder:checked===2}">
            <i class="select-type" @click="checked = 2" :class="{checked:checked===2}"></i><span>个人网银支付</span>
          </div>
        </div>
        <a class="submit">确认支付</a>
      </div>
    </div>
</template>

<script>
    export default {
        name: "order",
      data(){
        return{
          checked:1
        }
      },
      created(){
        let data = {
          top:false,
          nav:false,
          foot:false
        };
        this.$store.dispatch('layout/setLayout', data);
      }
    }
</script>

<style scoped lang="stylus">
  a.back
    color #1372fa
  h1
    margin-top 50px
    margin-bottom 20px
    font-size 30px
  .order-detail
    position relative
    width 100%
    height 120px
    padding-bottom 20px
    border-bottom 1px solid #ccc
    &:after
      clear both
      display block
      content ""
    .icon
      width 100px
      height 100px
      float left
      margin-right 20px
      img
        width 100%
        height 100%
        display block
        background-color #85827d
    .bill
      width 300px
      height 100px
      float left
      padding 10px 0
      font-size 16px
      p
        line-height 40px
    .total
      height 100px
      line-height 100px
      font-size 16px
      float right
  .pay-type
    position relative
    width 100%
    padding 50px 0
    .pay
      padding 0 20px
      height 60px
      line-height 60px
      border #ffffff solid 2px
      &.checkborder
        border #038aeb solid 2px
      .select-type
        border: 1px solid #ccc;
        width: 20px;
        height: 20px;
        margin-right 20px
        display: inline-block;
        vertical-align: middle;
        cursor pointer
        &.checked
          background-image url("/static/pay/shop-select.png")
          background-repeat: no-repeat;
          background-position: center;
      input[type='radio']
        width: 17px;
        height: 17px;
        vertical-align: middle;
        margin-left: 20px;
      label
        display inline-block
        width 40px
        height 40px
        vertical-align: -15px;
        &.ali
          background-image url("/static/pay/shop-alipay.png")
          background-repeat: no-repeat;
          background-position: center;
        &.tencent
          background-image url("/static/pay/shop-wechat.png")
          background-repeat: no-repeat;
          background-position: center;
  .submit
    display block
    width 120px
    height 40px
    line-height 40px
    cursor pointer
    color white
    background-color #fe7405
    margin 160px auto 0
    text-align center
    font-size 18px
</style>



