<template>
  <div class="full-width demand">
    <div class="container">
      <div class="table">
        <div class="table-box table-select">
          <div class="tr">您的选择:</div>
          <div class="td row">
            <div class="td-select" v-for="(i,index) in selected" @click="del(index)" :key="index">
              {{i.name}}
            </div>
          </div>
        </div>
        <div class="table-box table-service">
          <div class="tr">选择服务:</div>
          <div class="td row">
            <div class="" v-for="i in Category" style="margin-bottom: 25px">
              <div class="td-tile">{{i.name}}</div>
              <div class="td-box">
                <div class="td-item" :class="{select:j}" v-for="j in i.children" @click="selec(j)">{{j.name}}</div>
              </div>
            </div>
          </div>
        </div>
        <div class="table-box table-desc">
          <div class="tr">需求描述：</div>
          <div class="td row">
            <textarea v-model="service_desc" name="describe"  cols="30" rows="10"></textarea>
          </div>
        </div>
        <div class="table-box msg">
          <div class="tr">电话号码：</div>
          <div class="td row">
            <input v-model="mobile" name="phone" class="phone2" placeholder="请输入手机号码" cols="30" rows="10">
          </div>
        </div>
        <div class="table-box code">
          <div class="tr">验证码：</div>
          <div class="td row">
            <input v-model="verify_code" name="code" id="code" placeholder="请输入手机验证码" cols="30" rows="10">
            <label v-if="btn" class="codelabel" for="code" autocomplete="none" @click="sendMss">{{getCode}}</label>
            <label v-else class="codelabel" for="code" autocomplete="none" @click="sendMss">{{getCode}}</label>
          </div>
        </div>
        <a class="btn" @click="sub">提交需求</a>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "demand",
        data(){
          return{
            btn:true,
            getCode:'获取验证码',
            verify_code:'',
            mobile:'',
            service_desc:'',
            selected:[],
            Category:[]
          }
        },
      filters:{
      },
      created(){
        let data = {
          top:true,
          nav:true,
          foot:true
        };
        this.$store.dispatch('layout/setLayout', data);
          this.getCategory()
      },
      computed:{
        service_id(){
          let id =this.selected.map(x=> x.id);
          return id
        }
      },
      methods:{
        distinct(arr){
          let result = [];
          let len = arr.length;
          arr.forEach(function(v, i ,arr){  //这里利用map，filter方法也可以实现
            let bool = arr.indexOf(v,i+1);  //从传入参数的下一个索引值开始寻找是否存在重复
            if(bool === -1){
              result.push(v);
            }
          });
          return result;
        },
        sendMss(){
          let time = 60;
          this.btn = false;
          let _self = this;
          _self.getCode = `请等待${time}秒`;
          let timer = setInterval(function () {
            time -=1;
            _self.getCode = `请等待${time}秒`;
            if (time === 0){
              _self.btn = true;
              _self.getCode = '获取验证码';
              clearInterval(timer)
            }
          },1000);
          this.http('api/verify/sendSms',{phone:this.mobile}).then(
            res=>{
              if (res.data.status === 200){
              }else{
                clearInterval(timer);
                _self.getCode = '获取验证码';
              }
            }
          ).catch(req=>{
            clearInterval(timer);
            _self.getCode = '获取验证码';
          })
        },
        getCategory(){
          this.http('api/index/getCategory').then(res=>{
            if (res.data.status===200){
              this.Category = res.data.data
            }
          })
        },
        selec(i){
          this.selected.push(i);
          this.selected = this.distinct(this.selected)
        },
        del(key){
          this.selected.splice(key,1)
        },
        sub(){
          let data = {
            mobile:this.mobile,
            service_desc:this.service_desc,
            service_id:this.service_id,
            verify_code:this.verify_code,
          };
          this.http('api/demand/saveDemand',data).then(res=>alert(res.data.msg)).catch(err=>alert('参数错误'))

          //todo
        }
      }

    }
</script>

<style scoped lang="stylus">
  .full-width.demand
    margin-top 1px
    background-color #f5f5f5
    padding 50px 0 200px 0
    .table
      position relative
      width 1020px
      margin 0 auto
      background-color white
      border-radius 10px
      padding 50px
      .table-box
        position relative
        margin-bottom 20px
        &:after
          display block
          clear both
          content ''
        .tr
          float left
          font-size 16px
          font-weight bold
          line-height 24px
          width 80px
          margin-right 30px
        .td.row
          float left
          position relative
          width 810px
          line-height 24px
          :after
            display block
            clear both
            content ''
          .td-select
            width 15%
            height 20px
            float left
            color #038aeb
            margin-bottom 20px
            cursor pointer
            &:hover
              text-shadow #038aeb 0 0 2px
              transition 0.5s
          .td-tile
            width 60px
            font-weight bold
            float left
            margin-right 25px
          .td-box
              float left
              width 700px
              :after
                display block
                clear both
                content ''
              .td-item
                width 16%
                float left
                margin-bottom 20px
                cursor pointer
          textarea
            width 700px
            height 150px
            resize none
            padding 10px
          input
            width 270px
            height 37px
            border none
            border-bottom 1px solid #ccc
            outline none
            vertical-align top
            line-height 37px
            padding 0 0 10px 20px
          .codelabel
            position absolute
            left 180px
            cursor pointer
            &:before
              display inline-block
              content ""
              width 1px
              height 16px
              background-color #ccc
              margin-right 10px
              transform translateY(3px)
      a.btn
        width 160px
        height 40px
        line-height 40px
        border 1px solid #038aeb
        border-radius 6px
        display block
        text-align center
        color #038aeb
        font-size 18px
        margin 50px auto 0
        &:hover
          background-color #038aeb
          color #ffffff
</style>




